import React from 'react';
import './styles/loading.scss';
import './styles/typing.scss';

export default class Loading extends React.Component {
    render() {
        return (
            <div className='container-fluid loading' id='loading'>
                <div className='row'>
                    <div className='col-sm-12'>
                        <div className='my-loading code-font' id='my-loading'>
                            <p className='typing typing1'>
                                ReactDOM<span style={{color:'rgb(136, 198, 190)'}}>.</span><span style={{color:'rgb(121, 182, 242)'}}>render</span><span style={{color:'rgb(136, 198, 190)'}}>(</span>
                            </p>
                            <p className='typing typing2' style={{marginLeft:'50px'}}>
                            <span style={{color:'rgb(136, 198, 190)'}}>
                                &#60;</span><span style={{color:'rgb(252, 146, 158)'}}>Rynand Wessels </span><span style={{color:'rgb(136, 198, 190)'}}>/&#62;,</span>
                            </p>
                            <p className='typing typing3' style={{marginLeft:'50px'}}>
                                document<span style={{color:'rgb(136, 198, 190)'}}>.</span><span style={{color:'rgb(121, 182, 242)'}}>getElementById</span><span style={{color:'rgb(136, 198, 190)'}}>(</span><span style={{color:'rgb(141, 200, 145)'}}>'root'</span><span style={{color:'rgb(136, 198, 190)'}}>)</span>                           
                            </p>
                            <p className='typing typing4'>
                                <span style={{color:'rgb(136, 198, 190)'}}>); </span>
                            </p>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}